<template>
    <div>
        <el-table :data="items">
            <el-table-column prop="_id" label="ID" width="240">
            </el-table-column>
            <el-table-column prop="name" label="">
            </el-table-column>
            <el-table-column prop="avatar" label="">
                <template slot-scope="scope">
                    <img :src="scope.row.avatar" style="height:3rem">
                </template>
            </el-table-column>
            <el-table-column
            fixed="right"
            label=""
            width="180">
                <template slot-scope="scope">
                    <!--  -->
                    <el-button @click="$router.push(`/heroes/edit/${scope.row._id}`)" type="text" size="small"></el-button>
                    <el-button @click="remove(scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
export default {
    data() {
        return {
            items: []
        }
    },
    methods: {
        async fetch() {
            const res = await this.$http.get('rest/heroes')
            this.items = res.data
        },
        async remove(row) {
            this.$confirm(`,${row.name}`, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
                }).then( async () => {
                    let res = await this.$http.delete(`rest/heroes/${row._id}`)
            this.$message({
                type: 'success',
                message: '!'
            });
            this.fetch()
            })
            }
    },
    created() {
        this.fetch()
    }
}
</script>

