<template>
    <div class="about">
        <h1>{{id ? '' : ''}}</h1>
        <el-form label-width="120px" @submit.native.prevent="save">
            <el-tabs value = "basic" type="border-card">
                <el-tab-pane label="" name="basic">
                    <el-form-item label = "">
                        <el-input v-model="model.name"></el-input>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-input v-model="model.title"></el-input>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-select v-model="model.categories" multiple>
                            <el-option v-for="item in categories" 
                            :key = "item._id" :label = "item.name" :value = "item._id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label = "">
                        <!-- $setvue$set -->
                        <el-upload
                        class="avatar-uploader"
                        :action="uploadUrl"
                        :headers="getAuthHeaders()"
                        :show-file-list="false"
                        :on-success="res => $set(model,'avatar',res.url)"
                        >
                            <img v-if="model.avatar" :src="model.avatar" class="avatar" />
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-upload
                        class="avatar-uploader"
                        :action="uploadUrl"
                        :headers="getAuthHeaders()"
                        :show-file-list="false"
                        :on-success="res => $set(model,'banner',res.url)"
                        >
                            <img v-if="model.banner" :src="model.banner" class="avatar" />
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-rate style="margin-top:0.7rem" :max = "9" show-score v-model="model.scores.difficult"></el-rate>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-rate style="margin-top:0.7rem" :max = "9" show-score v-model="model.scores.skills"></el-rate>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-rate style="margin-top:0.7rem" :max = "9" show-score v-model="model.scores.attack"></el-rate>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-rate style="margin-top:0.7rem" :max = "9" show-score v-model="model.scores.survive"></el-rate>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-select v-model="model.items1" multiple>
                            <el-option v-for="item in items" 
                            :key = "item._id" :label = "item.name" :value = "item._id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-select v-model="model.items2" multiple>
                            <el-option v-for="item in items" 
                            :key = "item._id" :label = "item.name" :value = "item._id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-input type = "textarea" v-model="model.usageTips"></el-input>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-input type = "textarea" v-model="model.battleTips"></el-input>
                    </el-form-item>
                    <el-form-item label = "">
                        <el-input type = "textarea" v-model="model.teamTips"></el-input>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label = "" name = "skills">
                    <el-button @click="model.skills.push({})" type = "primary">
                        <i class="el-icon-circle-plus"></i>
                        
                    </el-button>
                    <el-row>
                        <el-col :md = "12" v-for="(item,i) in model.skills" :key="i">
                            <el-form-item label = "">
                                <el-input type = "text" v-model="item.name"></el-input>
                            </el-form-item>
                            <el-form-item label = "">
                                <el-input type = "text" v-model="item.delay"></el-input>
                            </el-form-item>
                            <el-form-item label = "">
                                <el-input type = "text" v-model="item.cost"></el-input>
                            </el-form-item>
                            <el-form-item label = "">
                                <!-- vue -->
                                <el-upload
                                class="avatar-uploader"
                                :action="uploadUrl"
                                :headers="getAuthHeaders()"
                                :show-file-list="false"
                                :on-success="(res) => $set(item,'icon',res.url)"
                                >
                                    <img v-if="item.icon" :src="item.icon" class="avatar" />
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </el-form-item>
                            <el-form-item label = "" >
                                <el-input type = "textarea" v-model="item.description"></el-input>
                            </el-form-item>
                            <el-form-item label = "" >
                                <el-input type = "textarea" v-model="item.tips"></el-input>
                            </el-form-item>
                            <el-form-item >
                                <el-button type = "danger" size = "small" @click="model.skills.splice(i,1)"></el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-tab-pane>
                <!--  -->
                <el-tab-pane label = "" name = "partners">
                    <el-button @click="model.partners.push({})" type = "primary">
                        <i class="el-icon-circle-plus"></i>
                        
                    </el-button>
                    <el-row type="flex" style="flex-wrap: wrap">
                        <el-col :md = "12" v-for="(item,i) in model.partners" :key="i">
                            <el-form-item label = "">
                                <!-- filterable -->
                                <el-select
                                filterable
                                 v-model="item.hero">
                                    <!-- valuelabel -->
                                    <el-option v-for="hero in heroes" 
                                    :key="hero._id"
                                    :value="hero._id"
                                    :label="hero.name"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label = "" >
                                <el-input type = "textarea" v-model="item.description"></el-input>
                            </el-form-item>
                            <el-form-item >
                                <el-button type = "danger" size = "small" @click="model.partners.splice(i,1)"></el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-tab-pane>
            </el-tabs>
            <el-form-item style="margin-top: 1rem;">
                    <!-- submit -->
                    <el-button type = "primary" native-type = "submit"></el-button>
            </el-form-item>
        </el-form>
    </div>
    
</template>

<script>
export default {
    props: {
        id: {}
    },
    data() {
        return {
            categories: [],
            items: [],
            heroes: [],
            model: {
                name:'',
                avatar:'',
                scores: {},
                skills: [],
                partners: [],
            },
        }
    },
    methods: {
        async save() {
            
            if(this.id){
                await this.$http.put(`rest/heroes/${this.id}`,this.model)
            }else{
                await this.$http.post('rest/heroes',this.model)
            }
            // 
            // this.$router.push('/heroes/list')
            this.$message({
                type: 'success',
                message: ''
            })
        },
        async fetch() {
            const res = await this.$http.get(`rest/heroes/${this.id}`)
            // res.datathis.model
            this.model = Object.assign({}, this.model, res.data)
        },
        // 
        async fetchCategories() {
            const res = await this.$http.get(`rest/categories`)
            this.categories = res.data
        },
        // 
        async fetchItems() {
            const res = await this.$http.get(`rest/items`)
            this.items = res.data
        },
        // 
        async fetchHeroes() {
            const res = await this.$http.get(`rest/heroes`)
            this.heroes = res.data
        },
        
    },
    created() {
        this.fetchItems()
        this.fetchCategories()
        this.fetchHeroes ()
        this.id && this.fetch()
    }
}
</script>
<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 5rem;
    height: 5rem;
    line-height: 5rem;
    text-align: center;
  }
  .avatar {
    width: 5rem;
    height: 5rem;
    display: block;
  }
</style>